/* General Reset */
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border-width: 0;
  border-style: solid;
  border-color: #e5e7eb;
}

/* Dashboard Layout */
.dashboard-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
}

/* Flexbox Chart Layout */
.chart-container {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  width: 100%;
}

/* Labels Section */
.labels-container {
  width: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Label Styling */
.label-item {
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  white-space: nowrap;
}

/* Chart Wrapper */
.bar-chart-wrapper {
  flex: 1;
  min-width: 0;
}

/* Tooltip Styling */
.recharts-tooltip-wrapper {
  pointer-events: auto;
}

/* Tooltip Block */
.tooltip {
  display: block;
  position: absolute;
  z-index: 1000;
  pointer-events: none;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .dashboard-container {
    padding: 8px;
  }

  .labels-container {
    width: 100px;
  }
}
